import { h, Component } from 'preact';
import Slider from 'react-slick';

import style from './style';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

export default class Logos extends Component {
  render () {
    const settings = {
      autoplay: true,
      dots: true,
      infinite: true,
      dotsClass: 'slick-dots',
      useTransform: false,
      arrows: true,
    };
    return (
			<div class={style.container}>
  	    <Slider {...settings}>
					<div><img src='../../assets/logos/71xUT8vOGBL._RI_.jpg' /></div>
					<div><img src='../../assets/logos/91i3vqS20AL._RI_.jpg' /></div>
					<div><img src='../../assets/logos/92f1d28015d4a1e7b125c6ca46a6786e.jpg' /></div>
					<div><img src='../../assets/logos/20957.jpg' /></div>
  	    </Slider>
			</div>
    );
	}
}



